import React from "react";
import { Pressable, Text, StyleSheet } from "react-native";
import { Colors } from "../../../config/Colors";
import { UI } from "../../../config/constants";

type Props = {
  id: string;
  body: string;
  isCorrect: boolean;
  index: number;
  isHighlighted: boolean;
  onPress: (id: string) => void;
};

const AnswerButton: React.FC<Props> = props => {
  const { id, body, isCorrect, index, isHighlighted, onPress } = props;

  const handlePress = () => {
    onPress(id);
  };

  const highightedStyle = {
    backgroundColor: isCorrect ? Colors.success : Colors.error,
    color: "#FFF",
  };

  return (
    <Pressable
      style={[styles.component, isHighlighted ? highightedStyle : {}]}
      onPress={handlePress}>
      <Text>
        {["a", "b", "c"][index]}
        {". "}
        {body}
      </Text>
    </Pressable>
  );
};

const styles = StyleSheet.create({
  component: {
    padding: 16,
    backgroundColor: Colors.secondary,
    marginBottom: UI.margin * 2,
    borderRadius: UI.borderRadius,
    width: "100%",
    shadowColor: "black",
    shadowOffset: UI.shadowOffset,
    shadowOpacity: 0.2,
    shadowRadius: UI.borderRadius,
  },
});

export default AnswerButton;
